<template>
  <div>
    <h1>Enter Name and Phone</h1>
    <form @submit.prevent="submitForm">
      <div>
        <label for="name">Name:</label>
        <input type="text" id="name" v-model="name" required>
      </div>
      <div>
        <label for="phone">Phone:</label>
        <input type="tel" id="phone" v-model="phone" required>
      </div>
      <button type="submit">Submit</button>
    </form>
  </div>
</template>

<script setup>
import { ref, watchEffect } from 'vue';
import { useQuery, useMutation } from '@vue/apollo-composable';
import { gql } from '@apollo/client/core';

// GraphQL Queries and Mutations
const CHECK_CONTACT = gql`
  query CheckContact($phone: String!) {
    contacts(filters: { phone: { eq: $phone } }) {
      data {
        id
        attributes {
          name
          phone
        }
      }
    }
  }
`;

const ADD_CONTACT = gql`
  mutation AddContact($name: String!, $phone: String!) {
    createContact(data: { name: $name, phone: $phone }) {
      data {
        id
        attributes {
          name
          phone
        }
      }
    }
  }
`;

const UPDATE_CONTACT = gql`
  mutation UpdateContact($id: ID!, $name: String!, $phone: String!) {
    updateContact(id: $id, data: { name: $name, phone: $phone }) {
      data {
        id
        attributes {
          name
          phone
        }
      }
    }
  }
`;

// Refs for form fields
const name = ref('');
const phone = ref('');

// useMutation hooks for adding and updating contacts
const { mutate: addContact } = useMutation(ADD_CONTACT);
const { mutate: updateContact } = useMutation(UPDATE_CONTACT);

const submitForm = () => {
  // Use useQuery to check for existing contact
  const { result, loading, error } = useQuery(CHECK_CONTACT, {
    variables: { phone: phone.value },
    fetchPolicy: 'network-only',
  });

  watchEffect(() => {
    if (loading.value) {
      console.log('Loading...');
      return;
    }

    if (error.value) {
      console.error('Error fetching contact:', error.value);
      return;
    }

    const data = result.value?.contacts?.data;

    if (!data || data.length === 0) {
      console.log('Contact does not exist. Creating new contact.');
      addContact({
        variables: {
          name: name.value,
          phone: phone.value,
        },
      }).then(response => {
        console.log('Create Success:', response);
      }).catch(err => {
        console.error('Error creating contact:', err);
      });
      return;
    }

    const contact = data[0];

    console.log('Contact exists. Updating contact with ID:', contact.id);
    updateContact({
      variables: {
        id: contact.id,
        name: name.value,
        phone: phone.value,
      },
    }).then(response => {
      console.log('Update Success:', response);
    }).catch(err => {
      console.error('Error updating contact:', err);
    });
  });
};
</script>

